
/* grey__images__logo__chime */
.grey__images__logo__chime{
    @apply w-full p-4 bg-gray-100;
}

.chime__gray__container{
    @apply container mx-auto flex justify-center;
}

.chime__gray__container img{
    width: 195px;
    height: 32px;
    object-fit: contain;
}

/* chime__visa__card__first */
.chime__visa__card__first{
    @apply flex justify-around mb-9 items-center pt-20 container mx-auto;
}
.chime__visa__card__first h1{
    @apply text-4xl font-bold mb-4;
}

.chime__visa__hading p{
    @apply text-sm font-normal text-gray-600 leading-7 w-full;
}

.chime__visa__hading .learn__more{
    @apply border py-2.5 border-solid border-gray-800 mt-6 hover:bg-black transition-all ease-linear hover:text-white rounded-full text-base px-10;
}

.chime__visa__card__first img{
    width: 420px;
}

/* chime__visa__card__second */
.chime__visa__card__second{
    @apply flex justify-around items-center pt-20 pb-20 bg-gray-50;
}

.chime__visa__card__second img{
    width: 460px;
}

.chime__visa__card__second h1{
    @apply text-3xl font-bold mb-4;
}

.chime__visa__hading p{
    @apply text-sm font-normal text-gray-600;
}
/* chime__visa__card__second */
.chime__visa__card__third{
    @apply flex justify-around items-center pt-20 mb-10;
}

.chime__visa__card__third img{
    width: 440px;
}

.chime__visa__card__third h1{
    @apply text-3xl font-bold mb-4;
}

.chime__visa__hading p{
    @apply text-sm font-normal text-gray-600 leading-7;
    width: 338px;
}

/* get__started_-sections */
.get__started_-sections{
    @apply bg-[#f0fbf5] h-96 flex items-center;
}

.get__started_-sections .get__started__sections__container{
    @apply container mx-auto;
}
.get__started_-sections .get__started__sections__container .get__started__sections__text{
    @apply flex justify-around w-full;
}
.get__started_-sections .get__started__sections__container .get__started__sections__text h1{
    @apply text-3xl font-bold mb-2;
}

.get__started_-sections .get__started__sections__container .get__started__sections__text form{
    @apply flex gap-3 mt-5 items-center;
}
.get__started_-sections .get__started__sections__container .get__started__sections__text form input{
    @apply border border-solid border-gray-400 rounded-full;
    padding: 10px 10px 10px 28px;
}
.get__started_-sections .get__started__sections__container .get__started__sections__text img{
    width: 300px;
}


/* Respnsive */

@media (max-width: 640px) {

    .description{
        top: 4rem!important;
        left: 1rem!important;
    }

    .chime__banner__container h1{
        font-size: 30px!important;
        margin-top: 3rem!important;
    }
    .chime__banner__container .form__signup{
        display: flex!important;
        flex-direction: column!important;
        margin-top: 6rem!important;
        gap: 5px!important;
    }
    .chime__gray__container{
        @apply grid grid-cols-2 gap-3;
    }
    .right__side__arrow__btn, .left__side__arrow__btn{
        display: none!important;
    }
    .chime__banner__container .form__signup form input{
        width: 100%!important;
    }
    .chime__banner__container .form__signup button{
        width: 100%!important;
    }
    .sub__hading p{
        font-size: 11px;
    }
    .carousel-boullt {
        position: absolute;
        top: -31rem!important;
        left: -1rem!important;
    }
    .header__container button:nth-child(1) {
        display: none!important;
    }
    .left__side__row nav{
        @apply p-2;
    }
    .header__container button:nth-child(2) {
        @apply px-8 py-2;
    }
    .carousel-boullt span {
        width: 46px!important;
    }

    .chime__visa__card__first{
        @apply block pl-4 pr-4;
    }
    .chime__visa__card__first h1{
        @apply text-3xl;
    }
    .chime__visa__card__first p{
        @apply text-base w-full;
    }
    .chime__visa__card__first img{
        @apply mt-10;
    }
    .chime__visa__hading p{
        @apply w-full;
    }
    .chime__visa__card__second{
        @apply block pl-4 pr-4;
    }

    .second__chime_sec{
        @apply flex flex-col-reverse pb-2 pt-8;
    }
    .chime__visa__card__second img{
        @apply mt-8;
    }

    .chime__visa__card__third{
        @apply block pl-4 pr-4;
    }
    .chime__visa__card__third img{
        @apply mt-10;
    }

    .credit__sec{
        @apply flex flex-col-reverse;
    }
    .chime__visa__hading .earn__para{
        @apply text-[10px];
    }
    .chime__visa__card__third h1{
        @apply text-2xl;
    }
    .chime__visa__card__second h1{
        @apply text-2xl mt-5;
    }
    .get__started_-sections .get__started__sections__container .get__started__sections__text{
        @apply flex flex-col;
    }
    .get__started_-sections{
        @apply p-4;
        height: 484px;
    }
    .get__started_-sections .get__started__sections__container .get__started__sections__text form{
        @apply flex flex-col gap-3;
    }
    .get__started_-sections .get__started__sections__container .get__started__sections__text form input{
        @apply w-full;
    }
    .get__started_-sections .get__started__sections__container .get__started__sections__text form button{
        @apply w-full mb-8;
    }

}