.banner-contaner-slider{
    @apply min-h-screen relative;
}
.description{
    @apply absolute top-48 left-56;
}
.carousel-boullt{
    @apply flex items-center justify-center absolute -bottom-16 left-48;
}
.carousel-boullt span{
    @apply w-[70px] h-[3px] ml-4 rounded-full cursor-pointer;
    background-color: rgba(255,255,255,0.4);
}

.right__side__arrow__btn{
    @apply absolute right-6 top-80 w-8 h-8 border-2 border-solid flex justify-center items-center rounded-full border-white hover:opacity-100;
    opacity: .7;
}
.left__side__arrow__btn{
    @apply absolute left-12 top-80 w-8 h-8 border-2 border-solid flex justify-center items-center rounded-full border-white hover:opacity-100;
    opacity: .7;
}

.right__side__arrow__btn svg{
    @apply fill-white;
}
.left__side__arrow__btn svg{
    @apply fill-white;
}


.chime__banner__container{
    @apply container mx-auto;
}

.chime__banner__container h1{
    @apply text-7xl text-white font-bold;
}
.chime__banner__container .form__signup{
    @apply flex flex-row items-center gap-6 mt-8;
}
.chime__banner__container .form__signup form input{
    @apply w-96 h-12 text-base rounded-full border-none outline-none px-8;
}
.sub__hading{
    @apply pt-4 flex flex-col gap-4;
}
.sub__hading p{
    @apply text-white;
}

.carousel-boullt .active{
@apply relative;

}
